import useDocumentSize from '@cetus/hooks/src/useDocumentSize'
import { Box, Menu, MenuList, MenuListProps, MenuProps, Portal } from '@chakra-ui/react'
import React, { useEffect, useRef, useState } from 'react'

interface CetusMenuProps extends MenuProps {
  children: [React.ReactNode, React.ReactNode]
  listProps?: MenuListProps
}

function CetusMenu({ children, listProps = {}, isOpen, ...rest }: CetusMenuProps) {
  const btnRef = useRef<HTMLDivElement | null>(null)
  const [maxH, setMaxH] = useState(0)
  const {
    size: { h }
  } = useDocumentSize()

  useEffect(() => {
    if (isOpen) {
      const _bottomH = h - (btnRef.current?.getBoundingClientRect().bottom || 0) - 20
      const _topH = (btnRef.current?.getBoundingClientRect().top || 0) - 20
      setMaxH(_bottomH < 40 ? _topH : _bottomH)
    }
  }, [isOpen, h])

  return (
    <Menu isOpen={isOpen} {...rest}>
      <Box as="div" w="100%" ref={btnRef}>
        {children[0]}
      </Box>
      <Portal>
        <MenuList maxH={maxH} {...listProps}>
          {children[1]}
        </MenuList>
      </Portal>
    </Menu>
  )
}

export default CetusMenu
