import useDocumentSize from '@cetus/hooks/src/useDocumentSize'
import { Icon } from '@cetus/ui-kit'
import { Button, HStack, MenuButton, MenuListProps, PlacementWithLogical, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useDeepCompareEffect } from 'ahooks'
import { cloneElement, useState } from 'react'
import CetusMenu from './CetusMenu'
export const defaultFeeList: FeeTier[] = [
  {
    feeRate: '1',
    feeDisplay: '0.01%',
    title: 'Not Created',
    description: 'Best for very stable pairs.',
    fee: '0.0001',
    tickSpacing: '2'
  },
  {
    feeRate: '5',
    feeDisplay: '0.05%',
    title: 'Not Created',
    description: 'Best for stable pairs.',
    fee: '0.0005',
    tickSpacing: '10'
  },
  {
    feeRate: '10',
    feeDisplay: '0.1%',
    title: 'Not Created',
    fee: '0.001',
    tickSpacing: '20'
  },
  {
    feeRate: '25',
    feeDisplay: '0.25%',
    title: 'Not Created',
    description: 'Best for most pairs.',
    fee: '0.0025',
    tickSpacing: '60'
  },
  {
    feeRate: '100',
    feeDisplay: '1%',
    title: 'Not Created',
    description: 'Best for exotic pairs.',
    fee: '0.01',
    tickSpacing: '200'
  },
  {
    feeRate: '200',
    feeDisplay: '2%',
    title: 'Not Created',
    fee: '0.02',
    tickSpacing: '220'
  }
]

export type FeeTier = {
  title: string
  fee: string
  tickSpacing: string
  feeDisplay: string
  feeRate: string
  poolAddress?: string
  description?: string
  liquidity?: string
  tvl?: string
  disabled?: boolean
}

export type FeeTierSelectProps = {
  value?: FeeTier
  feeTierList?: FeeTier[]
  children: (props: any) => JSX.Element | JSX.Element
  onChange?: (feeTier: FeeTier) => void
  loading?: boolean
  wrapStyle?: MenuListProps
  placement?: PlacementWithLogical
  disabled?: boolean
}

function FeeTierSelect({ value, feeTierList, children, onChange, wrapStyle = {}, placement = 'bottom' }: FeeTierSelectProps) {
  const [list, setList] = useState<FeeTier[]>(defaultFeeList)
  const [open, setOpen] = useState(false)

  useDeepCompareEffect(() => {
    if (feeTierList) {
      setList(feeTierList)
    }
  }, [feeTierList])
  return (
    <CetusMenu
      isLazy
      isOpen={open}
      onClose={() => setOpen(false)}
      placement={placement}
      listProps={{
        p: '8px',
        borderRadius: '12px',
        w: '260px',
        overflowY: 'auto',
        ...wrapStyle
      }}
    >
      {typeof children === 'function'
        ? children({
            isOpen: open,
            onClick: () => setOpen(!open)
          })
        : cloneElement(children, {
            isOpen: open,
            onClick: () => setOpen(!open)
          })}
      <VStack w="100%" gap="8px" p="0">
        {list?.map(item => (
          <HStack
            as="button"
            w="100%"
            p="0 12px"
            h="52px"
            key={item?.feeRate}
            justify="space-between"
            disabled={item?.disabled}
            onClick={() => {
              onChange?.(item)
              setOpen(false)
            }}
            borderRadius="8px"
            border="1px solid"
            bg="bg_six"
            cursor={item?.disabled ? 'not-allowed' : 'pointer'}
            borderColor={value?.feeRate === item?.feeRate ? 'primary' : 'border'}
            sx={{
              _hover: {
                borderColor: 'primary_opacity.60'
              }
            }}
          >
            <Text color={value?.feeRate === item?.feeRate ? 'primary' : 'text_caption'} fontWeight="500">
              {item?.feeDisplay}
            </Text>
            <VStack align="flex-end" gap="4px">
              <Text fontSize="12px" color="text_caption">
                {item?.title}
              </Text>
              {item?.description && <Text fontSize="12px">{item?.description}</Text>}
            </VStack>
          </HStack>
        ))}
      </VStack>
    </CetusMenu>
  )
}

export default FeeTierSelect

export const FeeTierBlock = ({ value, feeTierList, onChange, loading, disabled }: Omit<FeeTierSelectProps, 'children'>) => {
  const { size } = useDocumentSize()
  return (
    <FeeTierSelect value={value} feeTierList={feeTierList} onChange={onChange} placement="bottom-end">
      {({ isOpen, onClick }) => (
        <MenuButton
          as={Button}
          variant="unstyled"
          isDisabled={disabled}
          p="3px"
          borderRadius="12px"
          border="1px solid"
          borderColor="border"
          display="flex"
          justifyContent="space-between"
          h={{ base: '40px', lg: '32px' }}
          w={{ base: '100%', lg: 'auto' }}
          cursor="pointer"
          onClick={onClick}
        >
          <HStack w="100%">
            <Text pl="8px" flex="1" textAlign="start">
              Fee Tier
            </Text>
            {loading ? (
              <Skeleton w="84px" h={{ base: '32px', lg: '24px' }} />
            ) : (
              <HStack p="4px 12px" borderRadius="8px" bg="primary_opacity.10" h={{ base: '32px', lg: '24px' }}>
                <Text color="text_highlight">{value?.feeDisplay || '--%'}</Text>
                <Icon
                  fontSize="12px"
                  xlinkHref="#icon-icon_arrow"
                  transition="transform 0.5s"
                  transform={isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
                />
              </HStack>
            )}
          </HStack>
        </MenuButton>
      )}
    </FeeTierSelect>
  )
}
